/**
 * @WordPress dependencies
 */
// import { __ } from '@wordpress/i18n';
import { registerFormatType, toggleFormat } from '@wordpress/rich-text';
import { RichTextToolbarButton } from '@wordpress/block-editor';

/**
 * @Internal dependencies
 */
import { LsIcon } from '@components';

const formats = window.arkbBlockVars?.customFormats || [];
formats.forEach((format) => {
	const name = format.name;
	const title = format.title;
	registerFormatType(name, {
		title,
		tagName: format.tagName,
		className: format.className,
		edit: ({ value, onChange, isActive }) => {
			return (
				<RichTextToolbarButton
					name='arkb-controls'
					title={title}
					icon={<LsIcon icon='LsArkheLogo' size='20' />}
					isActive={isActive}
					onClick={() => {
						return onChange(toggleFormat(value, { type: name }));
					}}
				/>
			);
		},
	});
});
