/**
 * @WordPress dependencies
 */
// import { __ } from '@wordpress/i18n';
import { registerFormatType } from '@wordpress/rich-text';
import { RichTextToolbarButton } from '@wordpress/block-editor';

// icon
import shortcodeIcon from './icon';

const callTexts = window.arkbBlockVars?.callTexts || [];

const shortcodeBtns = callTexts.map((text, i) => {
	return {
		name: `text-${i}`,
		label: text.label,
		content: text.content,
		icon: shortcodeIcon,
	};
});

if (shortcodeBtns.length > 0) {
	shortcodeBtns.forEach((data) => {
		const fillName = 'arkbShortcode';
		const formatName = `arkhe-blocks/shortcode-${data.name}`;
		const formatTitle = data.label;
		const btnIcon = data.icon;

		// 登録
		registerFormatType(formatName, {
			title: formatTitle,
			tagName: 'span',
			className: `shortcode-${data.name}`, // 何かないと動かない

			edit: ({ isActive, value, onChange }) => {
				return (
					<RichTextToolbarButton
						name={fillName}
						title={formatTitle}
						icon={btnIcon}
						isActive={isActive}
						onClick={() => {
							return onChange(
								// ※ insert部分を外部で関数化するとエラーになってしまう。
								wp.richText.insert(value, data.content, value.start, value.end)
							);
						}}
					/>
				);
			},
		});
	});
}
