/**
 * html文字列をminify
 */
export const minifyHtml = (html) => {
	if (!html) return '';

	// 改行先に削除
	html = html.replace(/\r?\n/g, '');

	// タブ → スペース
	html = html.replace(/\t/g, ' ');

	// 複数のスペースを１つに
	html = html.replace(/\s\s+/g, ' ');

	// タグとタグの間のスペースを削除
	html = html.replace(/> </g, '><');

	return html;
};

/**
 * svgを整える（直接入力されたsvg用）
 */
export const formatSvg = (svg) => {
	svg = minifyHtml(svg);

	// script削除
	svg = svg.replace(/<script>.*<\/script>/gim, '');

	// 念の為、xmlnsないときに付与
	if (-1 === svg.indexOf('xmlns=')) {
		svg = svg.replace('<svg', '<svg xmlns="http://www.w3.org/2000/svg"');
	}

	return svg;
};

/**
 * svgのpropsを修正（Gutenbergにバグがあった）
 */
export const fixSvgProps = (svg) => {
	if (!svg) return '';

	svg = svg.replace('viewbox', 'viewBox');
	svg = svg.replaceAll('strokewidth=', 'stroke-width=');
	svg = svg.replaceAll('strokelinecap=', 'stroke-linecap=');
	svg = svg.replaceAll('strokelinejoin=', 'stroke-linejoin=');

	return svg;
};

/**
 * svgをcssの image url に変換
 */
export const convertSvgToImgurl = (svg, encode) => {
	if (!svg) return '';

	if ('base64' === encode) {
		// memo: btoa() だけだとマルチバイト文字が入った時にエラーになる。
		// encodeURIComponent() でそれを回避できるがそれだとSVGとして描画できず、 unescape() と組み合わせることで期待する動作になった。 see: https://www.softel.co.jp/blogs/tech/archives/4133
		// svg = window.btoa(unescape(encodeURIComponent(svg)));

		// memo: Buffer 使えば非推奨な関数を使わなくてもいい see: https://hackersheet.com/naopoyo/sheets/bvtrkwt
		svg = Buffer.from(svg).toString('base64');
		return `url(data:image/svg+xml;base64,${svg})`;
	}

	// base64化しないときは念の為minify化
	svg = minifyHtml(svg);

	// カラーコードの先頭の # → %23 に置換
	svg = svg.replaceAll('="#', '="%23');
	return `url('data:image/svg+xml;utf8,${svg}')`;
};
