/**
 * paddingオブジェクトから値へ
 *
 * @param {Object} padding
 * @param {string} nullVal
 * @return {string} paddingの値
 */
export default function getCusomPadding(padding, nullVal) {
	const topPadding = padding?.top || nullVal;
	const rightPadding = padding?.right || nullVal;
	const bottomPadding = padding?.bottom || nullVal;
	const leftPadding = padding?.left || nullVal;
	return `${topPadding} ${rightPadding} ${bottomPadding} ${leftPadding}`;
}
