/**
 * @WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
// import { useSelect } from '@wordpress/data';
import { removeFormat } from '@wordpress/rich-text';
import { RichTextToolbarButton } from '@wordpress/block-editor';

const name = 'arkhe-blocks/clear';
const title = __('Clear format', 'arkhe-blocks');

export default {
	name,
	title,
	tagName: 'span',
	className: 'arkb-clear-fomat',
	edit: ({ value, onChange }) => {
		// 登録済みの全フォーマットを取得
		// const formatTypes = useSelect((select) => select('core/rich-text').getFormatTypes());

		return (
			<RichTextToolbarButton
				name='arkb-controls'
				icon='editor-removeformatting'
				title={title}
				isActive={false} // clear は常に false
				onClick={() => {
					const activeFormats = value.activeFormats;
					if (0 < activeFormats.length) {
						let newValue = value;
						activeFormats.map((format) => {
							newValue = removeFormat(newValue, format.type);
						});
						onChange({ ...newValue }); // newValue: removeFormat し終わった value
					}
				}}
			/>
		);
	},
};
