/**
 * @WordPress dependencies
 */
import { Slot, DropdownMenu, ToolbarItem } from '@wordpress/components';

import classnames from 'classnames';

// 配置
const POPOVER_PROPS = {
	position: 'bottom center',
};

/**
 * ArkbDropdownMenu
 */
export default ({ fillName, icon, label }) => {
	return (
		<Slot name={`RichText.ToolbarControls.${fillName}`}>
			{(fills) => {
				if (0 === fills.length) return;
				const allProps = fills.map(([{ props }]) => props);
				const hasActive = allProps.some(({ isActive }) => isActive);
				return (
					<ToolbarItem>
						{(toggleProps) => (
							<DropdownMenu
								icon={
									<>
										{icon}
										<span className='components-dropdown-menu__indicator'></span>
									</>
								}
								label={label}
								toggleProps={{
									...toggleProps,
									className: classnames(toggleProps.className, {
										'is-pressed': hasActive,
									}),
								}}
								controls={fills.map(([{ props }]) => props)}
								popoverProps={POPOVER_PROPS}
							/>
						)}
					</ToolbarItem>
				);
			}}
		</Slot>
	);
};
