/**
 * 2つの配列に同じ値が含まれているかを判定する
 */
export default function hasDuplicateValueInArrays(arr1, arr2) {
	// 重複要素だけを取り出す。（値が空のものも省く）
	const duplicateValues = [...arr1, ...arr2].filter(
		(item) => !!item && arr1.includes(item) && arr2.includes(item)
	);

	// 重複要素があればtrue
	return duplicateValues.length > 0;
}
