/**
 * @WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { useState, useCallback, renderToString } from '@wordpress/element';
import { Fill, ToolbarButton, ToggleControl } from '@wordpress/components';
import { registerFormatType, applyFormat, create, insert } from '@wordpress/rich-text';

/**
 * @Inner dependencies
 */
import icon from './icon';
import { convertSvgToImgurl, fixSvgProps } from '@helper';
import { LsIcon, LsIconModal } from '@components';

const name = 'arkhe-blocks/inline-icon';
const title = __('Icon', 'arkhe-blocks');

const settings = {
	title,
	tagName: 'span',
	className: 'arkb-inline-icon',
	attributes: {
		className: 'class',
		style: 'style',
		iconName: 'data-icon',
		dataID: 'data-id',
		ariaHidden: 'aria-hidden',
	},
};

registerFormatType(name, {
	...settings,

	// eslint-disable-next-line no-unused-vars
	edit: ({
		value,
		onChange,
		//isActive,
		//activeAttributes
	}) => {
		const [isAdding, setIsAdding] = useState(false);
		const [hasMargin, setHasMargin] = useState(true);
		const [repeatMode, setRepeatMode] = useState(false);

		const setIcon = useCallback(
			(newIcon, type = 'name') => {
				let svgString;
				if ('html' === type) {
					svgString = newIcon;
				} else {
					svgString = renderToString(<LsIcon icon={newIcon} />) || '';
					svgString = fixSvgProps(svgString);
				}

				if (!svgString) return;
				// ただの半角スペース(' ')だとエディター上でカーソル見えない時があるので &emsp; でスペース作成
				const space = create({ html: '&emsp;' });

				// 半角スペースを先頭に追加し、そこにformat適用する
				let newValue = insert(value, space, value.start);
				newValue = applyFormat(
					newValue,
					{
						type: name,
						attributes: {
							className: hasMargin ? '-has-margin' : '-no-margin',
							iconName: 'name' === type ? newIcon : 'custom',
							dataID: '' + value.start,
							// ↓ シングルクォートが再編集時にさらにエスケープされてバグるので base64 化して扱う。
							style: `--the-icon-svg: ${convertSvgToImgurl(svgString, 'base64')}`,
							ariaHidden: 'true',
						},
					},
					value.start,
					value.start + 1
				);

				// カーソルを後ろに
				return onChange(newValue);
			},
			[hasMargin, onChange]
		);

		const renderBottom = () => {
			return (
				<>
					<ToggleControl
						className='u-arkb--mt--10'
						label={__('Add a little margin to the left and right', 'arkhe-blocks')}
						checked={hasMargin}
						onChange={(val) => {
							setHasMargin(val);
						}}
					/>
					<ToggleControl
						className='u-arkb--mt--10 u-arkb--mb--0'
						label={__('Continuous mode', 'arkhe-blocks')}
						checked={repeatMode}
						onChange={(val) => {
							setRepeatMode(val);
						}}
					/>
				</>
			);
		};

		// RichTextToolbarButton でカーソル位置にポップオーバー出してしまうと連続でアイコン挿入する時にずれていくので、Fillでツールバーに追加し、ポップオーバーの位置を固定する
		return (
			<Fill name='arkbIcon'>
				<ToolbarButton
					// isActive={isActive}
					name='arkbIcon'
					title={title}
					icon={icon}
					onClick={() => {
						setIsAdding(true);
					}}
				/>
				{isAdding && (
					<LsIconModal
						valur={''}
						position='center'
						onChange={(val) => {
							setIcon(val);
						}}
						onClose={() => setIsAdding(false)}
						onSetSvg={(val) => {
							setIcon(val, 'html');
						}}
						renderBottom={renderBottom}
						continueable={repeatMode}
					/>
				)}
			</Fill>
		);
	},
});
